import React, { Component } from "react";
import { connect } from "react-redux";
import { getClosePengambilanBarcode } from "../../actions/closeBarcodeAction";
import { hideModal } from "../../actions/ModalAction";
import { berhasil, gagal } from "../../Alert";
import { AxiosMasterPost } from "../../Axios";
import { getToday } from "../../getDate";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";

class ClosePengambilanBarangBarcode extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns_barang: [
        {
          dataField: "kode_jenis",
          text: "Kode Barang",
        },

        {
          dataField: "gross",
          text: "Gross",
        },
        {
          dataField: "pkg",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar",
        },
        {
          dataField: "kadar",
          text: "Harga(%)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",
          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos(RP)",
        },
      ],
    };
  }

  batalBarcode() {
    let data = {
      tanggal: getToday(),
      pengambilan_barcode: [
        {
          no_barcode: this.props.no_barcode,
        },
      ],
    };
    AxiosMasterPost("pengambilan-barcode/trx-close", data)
      .then(() => this.props.dispatch(hideModal()))
      .then(() => berhasil("Barcode Berhasil dibatalkan"))
      .then(() => this.props.dispatch(getClosePengambilanBarcode()))
      .catch((err) => gagal(`Error Batalkan Barcode , Detail : ${err}`));
  }
  render() {
    return (
      <div className="col-lg-12">
        <h3>Detail Barang</h3>
        <GlobalTabel
          keyField="kodeJenis"
          data={this.props.listCloseBarcodeSelected || []}
          columns={this.state.columns_barang}
          textEmpty="Silahkan Tambah Data Dengan Klik Tombol Biru Diatas"
        />
        <div className="col-lg-12">
          <div className="text-right">
            <button
              className="btn btn-danger"
              onClick={() => this.batalBarcode()}
            >
              <i className="fa fa-times mr-3"></i>
              Batal Barcode
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default connect((state) => {
  return {
    listCloseBarcodeSelected: state.provinsi.listCloseBarcodeSelected,
    no_barcode: state.provinsi.no_barcode,
  };
})(ClosePengambilanBarangBarcode);
