import React, { Component } from "react";
import { connect } from "react-redux";

import { reduxForm } from "redux-form";
import {
  getClosePengambilanBarcode,
  getClosePengambilanBarcodeSelected,
} from "../actions/closeBarcodeAction";
import { showModal } from "../actions/ModalAction";
import ModalMasterData from "../DataMaster/Component/ModalMasterData";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import HeaderCard from "../HeaderCard";
import ClosePengambilanBarangBarcode from "./Component/ClosePengambilanBarangBarcode";

const maptostate = (state) => {
  return {
    closePengambilanBarcode: state.provinsi.closePengambilanBarcode,
    listCloseBarcode: state.provinsi.listCloseBarcode,
  };
};

class ClosePengambilanBarcode extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [
        {
          value: "TERIMA SUPPLIER",
          name: "TERIMA SUPPLIER",
        },
        {
          value: "PENJUALAN",
          name: "PENJUALAN",
        },
        {
          value: "PINDAH BARANG",
          name: "PINDAH BARANG",
        },
        {
          value: "RETURN CUSTOMER",
          name: "RETURN CUSTOMER",
        },
      ],
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "baki",
          text: "Nama Baki",
        },
        {
          dataField: "instansi",
          text: "Supplier",
        },
        {
          dataField: "jenis_trx",
          text: "Kode Barang",
        },
        {
          dataField: "total_bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "total_netto",
          text: "Netto(Gr)",
        },
        {
          dataField: "total_ongkos",
          text: "Ongkos(Rp)",
        },
        {
          dataField: "total_gross",
          text: "Gross(Gr)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() =>
                    this.showDetail(row.detail_barang, row.no_barcode)
                  }
                >
                  Detail
                </button>
              </div>
            );
          },
        },
      ],

      data: [
        {
          tanggal: "2020",
        },
      ],
    };
  }

  showDetail(data, no_barcode) {
    this.props.dispatch(showModal());
    this.props.dispatch(getClosePengambilanBarcodeSelected(data, no_barcode));
  }
  componentDidMount() {
    this.props.dispatch(getClosePengambilanBarcode());
  }

  handleSubmit(data) {
    console.log(data);
  }
  selected(data) {
    console.log(data);
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-lg-12">
            <div className="card">
              <HeaderCard title="Batal Pengambilan Barang Barcode" />
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <GlobalTabel
                    keyField="kodeJenis"
                    data={this.props.listCloseBarcode || []}
                    columns={this.state.columns}
                    textEmpty="Silahkan Tambah Data Dengan Klik Tombol Biru Diatas"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
        <ModalMasterData
          content={<ClosePengambilanBarangBarcode />}
          title="Detail Barang"
        />
      </div>
    );
  }
}

ClosePengambilanBarcode = reduxForm({
  form: "ClosePengambilanBarcode",
  enableReinitialize: true,
})(ClosePengambilanBarcode);
export default connect(maptostate, null)(ClosePengambilanBarcode);
